import {createSlice} from "@reduxjs/toolkit";

interface sidebarCollapsetypes {
    collapsed: boolean;
}

const sidebarCollapseState: sidebarCollapsetypes = {
    collapsed: false,
}

const sidebarCollapseSlice = createSlice({
    name: 'sidebarCollpse',
    initialState: sidebarCollapseState,
    reducers: {
        sidebarCollapse (state){
            state.collapsed = !state.collapsed;
        }
    }}
)

export const sidebarCollapseActions = sidebarCollapseSlice.actions;

export default sidebarCollapseSlice;
