import React from "react";
import {Link} from "react-router-dom";

const AnchorTag: React.FC<{ to: string, classes?: string, caption: string }> = (props) => {
    return (
        <Link to={props.to} className={props.classes ? props.classes + ' ' + 'btn' : 'btn'}>{props.caption}</Link>
    );
}

export default AnchorTag;
