import React, {Fragment, useState} from 'react';
import MarkersListFn from "../../mapsFn/markerlist/MarkersListFn";

const PlugPointsTab = () => {
    const [collapse, setCollapse] = useState<boolean>(false);

    const collapseHandler = () => {
        setCollapse(!collapse)
    }
    return (
        <Fragment>
            <button className={collapse ? "tab-button collpsed" : "tab-button"} type="button" onClick={collapseHandler}>
                <span className="icon">
                    <svg xmlns="http://www.w3.org/2000/svg" width="17.6" height="22" viewBox="0 0 20.8 26">
                      <path id="plug"
                            d="M164.634,66.166a1.3,1.3,0,0,0,1.3,1.3h2.6v3.9a1.3,1.3,0,0,0,1.3,1.3h7.8a1.3,1.3,0,0,0,1.3-1.3v-3.9h2.6a1.3,1.3,0,0,0,1.3-1.3v-9.1h1.3v-2.6h-5.2v-7.8h-2.6v7.8h-5.2v-7.8h-2.6v7.8h-5.2v2.6h1.3Z"
                            transform="translate(-163.334 -46.666)" fill="currentColor"/>
                    </svg>
                </span>
                Near plugs
            </button>
            {collapse && <MarkersListFn isOpen={collapse}/>}
        </Fragment>
    )
}

export default PlugPointsTab;
