import React from "react";
import useLatlng from "../../../../hooks/use-latlng";
import useNavigation from "../../../../hooks/use-navigation";
import {useDispatch} from "react-redux";
import {directionActions} from "../../../../store/directionSlice";

const TripList: React.FC<{ list: { origin: string, destination: string} }> = (props) => {
    const dispatch = useDispatch();
    const {launchNavigation} = useNavigation();
    const {
        lat: originLat,
        lng: originLng,
        error: originError,
        status: originStatus,
        getGeocode: originGeocode,
    } = useLatlng();

    const {
        lat: destinationLat,
        lng: destinationLng,
        error: destinationError,
        status: destinationStatus,
        getGeocode: destinationGeocode,
    } = useLatlng();

    const showRouteHandler = () =>{
        dispatch(directionActions.originDestinationLocation({origin: props.list.origin, destination: props.list.destination}))
    }

    const statNavigation = (event: React.MouseEvent<HTMLButtonElement>) =>{
        event.stopPropagation();
        originGeocode(props.list.origin);
        destinationGeocode(props.list.destination);
        if(originStatus === 'OK' && destinationStatus === 'OK'){
            const origin = new google.maps.LatLng(originLat, originLng);
            const destination = new google.maps.LatLng(destinationLat, destinationLng);
            launchNavigation(origin, destination);
        }
    }

    return (
        <div className="bk-plug mb-1 cursor-pointer" onClick={showRouteHandler}>
            <span className="mb-1">Origin: <b>{props.list.origin}</b></span>
            <span>Destination: <b>{props.list.destination}</b></span>
            <button onClick={statNavigation}>
                <svg xmlns="http://www.w3.org/2000/svg" width="30" height="30" viewBox="0 0 30 30">
                    <path id="directions"
                          d="M12.895,23.317h2.233V18.478h6.849v3.164l4.318-4.28-4.318-4.318v3.2H14.011a1.082,1.082,0,0,0-1.117,1.117ZM18.85,33.85a2.645,2.645,0,0,1-.875-.149,1.885,1.885,0,0,1-.726-.447l-12.8-12.8A1.885,1.885,0,0,1,4,19.725a2.644,2.644,0,0,1,0-1.749,1.885,1.885,0,0,1,.447-.726l12.8-12.8A1.885,1.885,0,0,1,17.975,4a2.644,2.644,0,0,1,1.749,0,1.885,1.885,0,0,1,.726.447l12.8,12.8a1.885,1.885,0,0,1,.447.726,2.644,2.644,0,0,1,0,1.749,1.885,1.885,0,0,1-.447.726l-12.8,12.8a1.885,1.885,0,0,1-.726.447A2.645,2.645,0,0,1,18.85,33.85Z"
                          transform="translate(-3.85 -3.85)" fill="currentColor"></path>
                </svg>
            </button>
        </div>
    )
}

export default TripList;
