import {createSlice, PayloadAction} from "@reduxjs/toolkit";

interface directionTypes {
    origin: string,
    destination: string,
    stopPoints: { lat: number, lng: number }[] | null,
}

const directionSate: directionTypes = {
    origin: '',
    destination: '',
    stopPoints: null,
}

const directionSlice = createSlice({
    name: 'direction',
    initialState: directionSate,
    reducers: {
        originDestinationLocation(state, action: PayloadAction<{ origin: string, destination: string}>) {
            state.origin = action.payload.origin;
            state.destination = action.payload.destination;
        },
        stopOverPoints(state, action: PayloadAction<{stopPoints: { lat: number, lng: number }[] | null }>){
            state.stopPoints = action.payload.stopPoints
        }
    }
})

export const directionActions = directionSlice.actions;

export default directionSlice;
