import {createSlice, PayloadAction} from "@reduxjs/toolkit";

interface centetMarkerTypes {
    center: {lat: number | null, lng: number | null}
}

const centerMarkerState: centetMarkerTypes = {
    center: {lat: null, lng: null}
}

const nearbyCenterSlice = createSlice({
    name: "nearbyCenter",
    initialState: centerMarkerState,
    reducers: {
        setCenterState (state, action: PayloadAction<{lat: number, lng: number}>){
            state.center = action.payload;
        }
    }
})

export const nearbyCenterSliceActions = nearbyCenterSlice.actions;

export default nearbyCenterSlice;
