import React, { Fragment, Suspense } from 'react';
import { Routes, Route, Navigate } from 'react-router-dom';
import { useJsApiLoader } from '@react-google-maps/api';
import 'bootstrap/dist/css/bootstrap.min.css';
import './App.css';
import Layout from './components/layout/Layout';
import Loader from './components/ui/Loader';
import { LoadScript } from "@react-google-maps/api";

const Dashboard = React.lazy(() => import('./components/pages/dashboard/Dashboard'));
const AddNewMarker = React.lazy(() => import('./components/pages/AddNewMarker'));

function App() {
    const mapApi: string = '' + process.env.REACT_APP_MAP_API + '';

    return (
        <LoadScript googleMapsApiKey={mapApi} libraries={["places", "geometry"]}>
            <Layout>
                <Suspense fallback={<Loader />}>
                    <Routes>
                        <Route path='/' element={<Navigate to="/home" />} />
                        <Route path='/home' element={<Dashboard />} />
                        <Route path='/addnewmarker' element={<AddNewMarker />} />
                    </Routes>
                </Suspense>
            </Layout>
        </LoadScript>
    );
}

export default App;
