import {createSlice, PayloadAction} from "@reduxjs/toolkit";

interface initialLocationTypes {
    location: string;
}

const initialLocationState: initialLocationTypes = {
    location: '',
}

const searchLocatioSlice = createSlice({
    name: 'searchLocation',
    initialState: initialLocationState,
    reducers: {
        searchedLocation (state, action: PayloadAction<string>) {
            state.location = action.payload;
        }
    }
});

export const searchLocationActions = searchLocatioSlice.actions;

export default searchLocatioSlice;
