const useNavigation = () => {
    const launchNavigation = (origin: { lat: () => number, lng: () => number }, destination: { lat: () => number, lng: () => number }) => {
        if (google.maps.Map) {
            const url = `https://www.google.com/maps/dir/?api=1&origin=${origin.lat()},${origin.lng()}&destination=${destination.lat()},${destination.lng()}`;
            window.open(url);
        }
    };

    return {launchNavigation}
}

export default useNavigation;
