import {configureStore} from "@reduxjs/toolkit";

import markersSlice from "./markersSlice";
import searchLocatioSlice from "./searchLocatioSlice";
import directionSlice from "./directionSlice";
import sidebarCollapseSlice from "./sidebarCollapseSlice";
import nearbyCenterSlice from "./nearbyCenterSlice";

const store = configureStore({
    reducer: {
        markers: markersSlice.reducer,
        searchLocation: searchLocatioSlice.reducer,
        direction: directionSlice.reducer,
        sidebar: sidebarCollapseSlice.reducer,
        nearbyCenter: nearbyCenterSlice.reducer,
    }
})

export type RootState = ReturnType<typeof store.getState>

export default store;
