import React, {useState, useEffect} from 'react';
import {useSelector, useDispatch} from "react-redux";
import {nearbyCenterSliceActions} from "../../../store/nearbyCenterSlice";
import {RootState} from "../../../store";
import useMarkList from "../../../hooks/use-markList";
import List from "./List";

const MarkersListFn: React.FC<{ isOpen: boolean }> = (props) => {
    const [center, setCenter] = useState<{ lat: number | null, lng: number | null }>({lat: null, lng: null});
    const [nearmarkers, setNearMarkers] = useState<{ lat: number, lng: number, title: string, address: string }[]>([])
    const positions = useSelector((state: RootState) => state.markers.positions);
    const {markersList, markers} = useMarkList();
    const dispatch = useDispatch()

    useEffect(() => {
        if (props.isOpen) {
            navigator.geolocation.getCurrentPosition((position) => {
                setCenter({lat: position.coords.latitude, lng: position.coords.longitude});
                dispatch(nearbyCenterSliceActions.setCenterState({
                    lat: position.coords.latitude,
                    lng: position.coords.longitude
                }))
            });
        }
    }, [props.isOpen])

    useEffect(() => {
        if (center.lat && center.lng) {
            markersList(positions, center);
        }
    }, [center])

    useEffect(() => {
        setNearMarkers(markers)
    }, [markers])    

    return (
        <div className="mgt-1 mgb-2 overflow-auto mxh-6">
            {nearmarkers && <div>
                {
                    nearmarkers.slice(0, 5).map((item, index) => <List key={index} coordinates={[item.lat, item.lng]} title={item.title} address={item.address}/>)
                }
            </div>}
            {
                nearmarkers.length === 0  && <p>No charging stations near in this area</p>
            }
        </div>
    );
}

export default MarkersListFn;
