import {Autocomplete} from "@react-google-maps/api";
import React, {useRef} from "react";
import {directionActions} from "../../store/directionSlice";
import {useDispatch} from "react-redux";

const DirectionFn = () => {
    const originRef = useRef<HTMLInputElement>(null);
    const destinationRef = useRef<HTMLInputElement>(null);
    const dispatch = useDispatch();

    const submitHandler = (event: React.FormEvent) => {
        event.preventDefault();
        if (originRef.current!.value.length > 0 && destinationRef.current!.value.length > 0) {
            dispatch(directionActions.originDestinationLocation({
                origin: originRef.current!.value,
                destination: destinationRef.current!.value
            }))
            originRef.current!.value = '';
            destinationRef.current!.value= ''
        }
        return
    }
    return (
        <form onSubmit={submitHandler}>
            <Autocomplete className='form-group'>
                <input
                    className="form-control"
                    type="text"
                    placeholder="Origin"
                    ref={originRef}
                />
            </Autocomplete>
            <Autocomplete className="form-group">
                <input
                    className="form-control"
                    type="text"
                    placeholder="Destination"
                    ref={destinationRef}
                />
            </Autocomplete>
            <button className="btn" type="submit">submit</button>
        </form>
    )
}

export default DirectionFn;
