import React, {Fragment, useState, useEffect} from 'react';
import DirectionFn from "../../../mapsFn/DirectionFn";
import {useSelector} from "react-redux";
import {RootState} from "../../../../store";
import TripList from "./TripList";

const DirectionTab = () => {
    const [collapse, setCollapse] = useState<boolean>(false);
    const [tripList, setTripList] = useState<{ origin: string, destination: string }[]>([])
    const directionOrigin = useSelector((state: RootState) => state.direction.origin, (prevData, newData) => {
        return prevData.length === newData.length;
    })
    const directionDestination = useSelector((state: RootState) => state.direction.destination, (prevData, newData) => {
        return prevData.length === newData.length;
    });

    const collapseHandler = () => {
        setCollapse(!collapse)
    }

    useEffect(() => {
        if (directionOrigin && directionDestination) {
            if (tripList.findIndex(item => item.origin === directionOrigin) === -1 && tripList.findIndex(item => item.destination === directionDestination) === -1) {
                setTripList((prevState) => {
                    return (
                        [{
                            origin: directionOrigin,
                            destination: directionDestination
                        }, ...prevState]
                    )
                })
            }
        }
    }, [directionOrigin, directionDestination])

    return (
        <Fragment>
            <button className={collapse ? "tab-button collpsed" : "tab-button"} type="button" onClick={collapseHandler}>
                <span className="icon">
                    <svg xmlns="http://www.w3.org/2000/svg" width="30" height="26.274" viewBox="0 0 30 26.274">
                        <g id="noun-trip-3202994" transform="translate(-84 -47.039)">
                            <path id="Union_1" data-name="Union 1"
                                  d="M8.242,26.274c.474-.568,1.042-1.263,1.642-2.116H20.179a.693.693,0,0,0,.695-.474.665.665,0,0,0-.221-.821l-5.116-4.169a2.818,2.818,0,0,1-.979-2.779,2.854,2.854,0,0,1,2.021-2.147l5.242-1.484c.569.758,1.074,1.358,1.453,1.768L17.147,15.79h0a.752.752,0,0,0-.537.569.711.711,0,0,0,.253.726l5.116,4.137a2.846,2.846,0,0,1-1.8,5.053Zm-2.779-.221C5.242,25.832,0,20.495,0,15.6A5.7,5.7,0,0,1,5.684,9.916h0A5.681,5.681,0,0,1,11.369,15.6c0,4.863-5.242,10.231-5.463,10.453a.306.306,0,0,1-.442,0ZM2.81,15.6a2.874,2.874,0,1,0,2.874-2.874A2.884,2.884,0,0,0,2.81,15.6Zm22.327-2.305c-.19-.189-4.484-4.61-4.484-8.621A4.663,4.663,0,0,1,25.326,0h0A4.663,4.663,0,0,1,30,4.674c0,4.042-4.295,8.432-4.484,8.621a.447.447,0,0,1-.189.063A.232.232,0,0,1,25.137,13.295ZM22.958,4.674a2.368,2.368,0,1,0,2.368-2.368A2.386,2.386,0,0,0,22.958,4.674Z"
                                  transform="translate(84 47.039)" fill="currentColor"/>
                        </g>
                    </svg>
                </span>
                Plan a trip
            </button>
            {collapse &&
            <Fragment>
                <div className={'plan-trip mgb-2'}><DirectionFn/>
                    {
                        tripList.length > 0 && <div className="mgt-1 mgb-2 overflow-auto mxh-6">
                            <div>
                                {
                                    tripList?.slice(0, 10).map((item, index) => {
                                        return (
                                            <TripList key={index} list={item}/>
                                        )
                                    })
                                }
                            </div>
                        </div>
                    }
                </div>
            </Fragment>
            }
        </Fragment>
    )
}

export default DirectionTab
