import React, { useState, useEffect } from "react";
import { Link, useLocation } from 'react-router-dom';
import { useDispatch, useSelector } from "react-redux";
import { sidebarCollapseActions } from "../../../store/sidebarCollapseSlice";
import { RootState } from "../../../store/index";
import logo from '../../../images/logo.svg';
import LocationSearch from "./LocationSearch";
import AuthFeilds from "./AuthFeilds";

const Header = () => {
    const [isShow, setisShow] = useState<boolean>(false)
    const dispatch = useDispatch();
    const togglesidebarButtom = useSelector((state: RootState) => state.sidebar.collapsed);
    const location = useLocation();

    useEffect(() => {
        if (location.pathname === '/home') {
            setisShow(true);
        }
        else {
            setisShow(false);
        }
    }, [location])

    const sidebarToggleHandler = () => {
        dispatch(sidebarCollapseActions.sidebarCollapse());
    }
    return (
        <div className="bk-header border-bottom">
            <div className="container-fluid d-flex align-items-center justify-content-between">
                <div className="d-flex align-items-center">
                    <button className={togglesidebarButtom ? "sidebarbtn active" : "sidebarbtn"}
                        onClick={sidebarToggleHandler}><span></span><span></span><span></span></button>
                    <Link to="/" className="logo">
                        <img src={logo} alt="logo" />
                    </Link>
                </div>
                {isShow && <LocationSearch />}
                <div className="d-none d-sm-block">
                    <AuthFeilds />
                </div>
            </div>
        </div>
    );
}

export default Header;
