import React, { useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import { useLocation } from 'react-router-dom';
import { RootState } from "../../../store/index";
import { sidebarCollapseActions } from "../../../store/sidebarCollapseSlice";
import DirectionTab from "./directionTab/DirectionTab";
import PlugPointsTab from "./PlugPointsTab";
import AddNewPlug from "./AddNewPlug";
import AuthFeilds from "../header/AuthFeilds";

const Sidebar = () => {
    const sidebarCollapse = useSelector((state: RootState) => state.sidebar.collapsed);
    const dispatch = useDispatch();
    const location = useLocation();

    useEffect(() => {
        dispatch(sidebarCollapseActions.sidebarCollapse());
    }, [location])

    return (
        <div className={sidebarCollapse ? 'bk-sidebar active' : 'bk-sidebar'}>
            <div className='position-relative overflow-auto h-100 scrollbar-none'>
                <div className="mgb-2 d-sm-none">
                    <AuthFeilds />
                </div>
                <div className="mgb-1">
                    <DirectionTab />
                </div>
                <div className="mgb-1">
                    <PlugPointsTab />
                </div>
                {/* <div className="mgb-1">
                    <AddNewPlug />
                </div> */}
            </div>
        </div>
    )
}

export default Sidebar;
