import React, { useState } from 'react';


interface positions {
    lat: number,
    lng: number
}

const useMarkList = () => {
    const [markers, setMarker] = useState<any>([]); 
    const radiusInKm = 10;
    const degreesPerKm = 0.009;
    const radius = radiusInKm * degreesPerKm;       

    const markersList = (positions: positions[], center: { lat: number | null, lng: number | null }) => {
        if(center.lat && center.lng){
            const bounds = new google.maps.LatLngBounds(
                new google.maps.LatLng(center.lat - radius, center.lng - radius),
                new google.maps.LatLng(center.lat + radius, center.lng + radius)
            )

            const nearMarkers = positions.filter(marker => {
                const position = new google.maps.LatLng(marker.lat, marker.lng)
                return bounds.contains(position)
            })

            const centerlatLng = new google.maps.LatLng(center.lat, center.lng);

            nearMarkers.sort((markerA, markerB) => {
                const positionA = new window.google.maps.LatLng(markerA.lat, markerA.lng);
                const positionB = new window.google.maps.LatLng(markerB.lat, markerB.lng);
                const distanceA = window.google.maps.geometry.spherical.computeDistanceBetween(positionA, centerlatLng);
                const distanceB = window.google.maps.geometry.spherical.computeDistanceBetween(positionB, centerlatLng);
                return distanceA - distanceB;
              });

            setMarker(nearMarkers);
        }       
    }

    return { markersList, markers }
}

export default useMarkList;
