import React, {useRef, useState, useEffect} from "react";
import {Autocomplete} from "@react-google-maps/api";
import {useDispatch, useSelector} from "react-redux";
import {searchLocationActions} from "../../../store/searchLocatioSlice";
import MarkerList from "./MarkerList";
import {RootState} from "../../../store";

const LocationSearch = () =>{
    const [isOpen, setIsOpen] = useState<boolean>(false);
    const dispatch = useDispatch();
    const searchedLocation = useSelector((state: RootState) => state.searchLocation.location);
    const searchPlaceholder = useRef<HTMLInputElement>(null);

    const onPlaceChanged = () =>{
        dispatch(searchLocationActions.searchedLocation(searchPlaceholder.current!.value));
    }

    useEffect(() =>{
        if(searchedLocation){
            setIsOpen(true);
        }
        else{
            setIsOpen(false);
        }
    }, [searchedLocation]);

    document.addEventListener('click', function(){
        setIsOpen(false);
    })

    return(
        <div className="position-relative">
            <div className="location-search">
                <Autocomplete onPlaceChanged={onPlaceChanged}
                >
                    <input
                        type="text"
                        placeholder="Seach a place here"
                        ref={searchPlaceholder}
                    />
                    </Autocomplete>
            </div>
            {/* {isOpen && <MarkerList /> } */}
        </div>
    );
}

export default LocationSearch;
